import Image from "next/image";
export interface AvatarProps extends  React.HTMLAttributes<HTMLImageElement> {
  src?: string;
  alt: string;
  size: 16| 24 | 32 | 40 | 64;
}

const Avatar = ({ src="data:image/png;base64,", alt, size, className, ...props   }: AvatarProps) => {
  
  return <Image src={src} width={size} height={size} alt={alt} className={className} {...props} />
};

export { Avatar };